<template>
<div class="container">
  <div class="head_block">
        <div class="head back_quizzes"><div class="head_text">Онбординг</div></div>
  </div>
  <div class="question" v-for="q , in onboarding" :key="q">
      <p v-if="q.number == n">{{q.text}}</p>
  </div>
  <div class="membr_table_question">
    <div @click="(answer = a.text) && (answerId = onboarding[n - 1].id)" class="membr_question" v-for="a , in answers[n - 1]" :key="a">
      <div>{{a.text}}</div>
    </div>
  </div>
    <div @click="put()"  class="membr_question membr_answer" v-show="answer != ''">
      <div class="answer">
        мой ответ ответ: {{answer}}
        <p>отправить</p>
      </div>
    </div>
  <div class="step">
    <div @click="back()" v-show="n > 1" class="step_arrow_back">
      <img src='./../img/277.svg'>
    </div>
    <div @click="next()" v-show="n < this.onboarding.length" class="step_arrow_next">
      <img src='./../img/276.svg'>
    </div>
  </div>
</div>
  <div class="footer">
    <div class="footer_block">
      <div @click="rout('/quizzes?sign=' + this.token)">
        <img src="./../img/004.svg">
        <p>онбординг</p>
      </div>
    <router-link :to="'/Structute' + '?sign=' + this.token">
      <div>
        <img src="./../img/006.svg">
        <p>структура</p>
      </div>
    </router-link>
    <router-link :to="'/' + '?sign=' + this.token">
    <div>
      <img src="./../img/036.svg">
      <p>команда</p>
    </div>
    </router-link>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      onboarding: [],
      answers: [],
      n: '1',
      answer: '',
      answerId: '',
      user: [],
      token: window.location.hash.split('=')[1]
    }
  },
  async mounted() {
    const onboarding = await fetch('https://dev6.automatisation.art/empower/api/onboardings?user_id=1227049925&user_sign=' + this.token)
    const dataOnboarding = await onboarding.json();
    for (let i = 0; i <= dataOnboarding.length; i++) {
      const newRequest = {
        id: dataOnboarding[i].id,
        text: dataOnboarding[i].text,
        number: dataOnboarding[i].number,
        answers: dataOnboarding[i].answers,
      } 
    this.onboarding.push(newRequest);
    this.answers.push(newRequest.answers);
    }
  },
  methods: {
    Answers() {
      setTimeout(async() =>{ 
        console.log(this.answers)
      }, 500)
    },
    put() {
      fetch('https://dev6.automatisation.art/empower/api/onboardings/' + this.answerId + '?user_id=' + window.Telegram.WebApp.initDataUnsafe.user.id + '&user_sign=' + this.token + '&answer_id=' + this.answerId + '&answer_value=' + this.answer, {
        method: 'PUT' 
    })
    },
    next() {
      this.n = Number(this.n)
      this.n = this.n + 1
    },
    back() {
      this.n = Number(this.n)
      this.n = this.n - 1
    },
    rout(r) {
      this.$router.push(r);
    },

    useR() {
    setTimeout(async() =>{  
    const user = await fetch('https://dev6.automatisation.art/empower/api/users/' + window.Telegram.WebApp.initDataUnsafe.user.id + '?user_sign=' + this.user)
    const dataUser = await user.json();
    for (let i = 0; i <= dataUser.length; i++) {
        const newRequest = {
          n: dataUser[i].onboardingArticle,
        };
        this.user.push(newRequest);
    }
    }, 5)
    },
    },
  beforeMount(){
    this.Answers()
    this.useR()
  }
}
</script>
